// Variables (Import the skins variables.scss file when you use the 2sic standard skin)
$brand-primary: #1BC48E;

// Only for debug
.container-fluid { background: #EFEFEF !important; }

.app-news {
	// Column View
	&.app-news-column-view {
		.app-news-container {
			display: -webkit-flex;
			display: flex;
			-webkit-flex-direction: row;
			flex-direction: row;
			flex-flow: row wrap;

			.app-news-item {
				position: relative;
				flex-basis: 33%;
				padding: 20px;
				
				> div {
					background: #ffffff;
					width: 100%;
					height: 100%;
					padding: 20px 20px 20px 20px;
				}

				h2 {
					font-size: 20px;
					font-weight: 700;
					margin-bottom: 0;
				}

				p {
					font-size: 16px;
					margin-bottom: 40px;
				}

				.app-news-date,
				.app-news-categories {
					font-size: 12px;
					letter-spacing: 1px;
				}

				.app-news-date {
					color: #999999;
				}

				.app-news-categories > span {
					color: $brand-primary;
					text-transform: uppercase;
					cursor: pointer;
					
					transition: .3s ease color;

					&:hover {
						color: #000;
					}
				}

				.btn.btn-primary {
					position: absolute;
					bottom: 40px;
				}

				@media (max-width: 960px) {
					flex-basis: 50%;
				}

				@media (max-width: 530px) {
					flex-basis: 100%;
				}
			}
		}
	}

	.btn.btn-primary {
		background: transparent;
		color: $brand-primary;
		padding: 0;
		margin: 0;
		border: none;

		transition: .3s ease color;

		&:hover {
			color: #000;
		}
	}
}